// for flag reference codes: https://flagicons.lipis.dev/
.flag:before {
    content: " "; // background-image: url(../images/flags/de.svg);
    display: inline-block;
    width: 18px;
    height: 15px;
    background-repeat: no-repeat;
    background-size: contain;
}

.country-picker {
    position: absolute !important;
    right: 0px;
    width: 20px !important;
    height: 100%;
    top: 0px;
    .open .dropdown-menu {
        width: 300px !important;
    }
    .dropdown-toggle {
        padding: 0 !important;
        width: 20px !important;
        box-shadow: none !important;
        height: unset !important;
    }
    button>span.bs-caret>span {
        display: none;
    }
    .text {
        font-size: 14px;
    }
}

@include respond-below(md) {
    .country-picker {
        display: none !important;
    }
}



