    top: 25px;
    right: 25px; }

/* Message Bubbles */
.message-bubble {
  display: block;
  position: relative;
  margin-bottom: 25px;
  /* Message Bubble "me" */ }
  .message-bubble .message-avatar {
    position: absolute;
    left: 0;
    top: 0; }
    .message-bubble .message-avatar img {
      width: 50px;
      height: 50px; }
  .message-bubble .message-text {
    margin-left: 70px;
    background-color: #f4f4f4;
    border-radius: 4px;
    padding: 12px 18px;
    position: relative;
    display: inline-block;
    float: left;
    line-height: 25px; }
  .message-bubble.me .message-text {
    float: right; }
  .message-bubble .message-text p {
    font-size: 15px;
    padding: 0;
    margin: 0;
    line-height: 25px; }
  .message-bubble.me .message-avatar {
    left: auto;
    right: 0; }
  .message-bubble.me .message-text {
    margin-left: 0;
    margin-right: 70px;
    background-color: #2a41e8;
    color: #fff;
    position: relative; }
  .message-bubble .message-text:before {
    content: "";
    width: 0;
    height: 0;
    border-top: 6px solid transparent;
    border-bottom: 6px solid transparent;
    border-right: 6px solid #f4f4f4;
    left: -6px;
    right: auto;
    top: 19px;
    position: absolute; }
  .message-bubble.me .message-text:before {
    border-top: 6px solid transparent;
    border-bottom: 6px solid transparent;
    border-left: 6px solid #2a41e8;
    border-right: none;
    right: -6px;
    left: auto; }

/* Message Time Sign */
.message-time-sign {
  text-align: center;
  display: block;
  position: relative;
  margin-bottom: 25px; }
  .message-time-sign:before {
    content: "";
    height: 1px;
