.about .image {
  background: url(https://bootstrapmade.com/demo/themes/Bocor/assets/img/about.jpg) center center no-repeat;
  background-size: cover;
  min-height: 400px; }

.section-bg {
  background: linear-gradient(180deg, #f2f6fa 0%, #fff 100%); }

.icon-box--home {
  text-align: left; }
  .icon-box--home .icon {
    font-size: 48px !important;
    float: left;
    color: #fdc134 !important; }
  .icon-box--home h4 {
    margin: 5px 0 10px 60px; }

@media (max-width: 767px) {
  .icon-box--home {
    text-align: center; }
    .icon-box--home h4 {
      margin: 0; }
    .icon-box--home .icon {
      float: none; } }

.search-results-tag {
  padding: .25rem .5rem;
  font-size: .7rem;
  line-height: 1;
  border-radius: .2rem; }

.freelancer-rating--professionals {
  margin-bottom: 0;
  visibility: hidden; }

.certification-container {
  display: flex;
  justify-content: flex-end;
  align-items: center; }

.certificate {
  width: 50px;
  margin-left: 15px; }

.dropdown-menu.open {
  max-height: 303px;
  overflow: hidden; }

.freelancer-overview .freelancer-avatar img {
  width: 65px;
  height: 65px;
  border-radius: 50%;
  cursor: pointer;
  max-width: unset; }

.sidebar-search-button-container {
  position: relative;
  background-color: transparent; }

@media (max-width: 1199px) {
  .freelancers-list-layout .freelancer-details {
    padding-top: 10px; }
  .freelancers-list-layout .freelancer-overview {
    padding-bottom: 0px; } }

@media (max-width: 991px) {
  .certification-container {
    justify-content: flex-start; }
  .certificate {
    margin-left: 0;
    margin-right: 10px; } }
